import {customElement} from '@here/hds-base';
import {html, LitElement, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styleMap} from 'lit/directives/style-map.js';
import {ListItem, ListItemSelectPayload, ListItemSize} from './hds-list-item.js';
import {styles} from './hds-list.css.js';

export type ListVariant = 'subtle' | 'subtle-divider' | 'solid';
export type ListPadding = 'default' | 'dense';

export type ListSelectPayload = {id: string; index: number};

/**
 * @slot default Content for the `hds-list-item` elements
 *
 * @event {CustomEvent<{id: string; index: number}>} select Fired when an item has been selected `ListSelectPayload`
 */
@customElement('hds-list')
export class List extends LitElement {
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  static listCounter = 0;
  @property({type: String, attribute: 'inner-role'}) innerRole: string | null = null;
  @property({type: String, attribute: 'inner-aria-label'}) innerAriaLabel: string | null = null;
  @property({type: Boolean}) truncate = true;
  @property({type: String, attribute: 'group-title'}) groupTitle = '';
  @property({type: String}) width;
  @property({type: String, attribute: true}) variant: ListVariant = 'subtle';
  @property({type: String, attribute: true}) size: ListItemSize = 'small';
  @property({type: String, attribute: 'horizontal-padding', reflect: true})
  horizontalPadding: ListPadding = 'default';
  @property({type: Number, attribute: 'selected-index', reflect: true}) selectedIndex = -1;
  @query('slot') protected itemsSlot!: HTMLElement;

  connectedCallback() {
    super.connectedCallback();
    this.id = this.id || `hds-list-${++List.listCounter}`;
  }

  protected updated(_changedProperties: PropertyValues) {
    if (_changedProperties.has('selectedIndex') && this.selectedIndex > -1) {
      const selectedElement = this._getAllItems()[this.selectedIndex];
      for (const item of this._getAllItems()) {
        item.selected = item.id === selectedElement.id;
      }
      this.dispatchEvent(
        new CustomEvent<ListSelectPayload>('select', {
          detail: {index: this.selectedIndex, id: selectedElement.id},
          bubbles: true,
          composed: true,
        })
      );
    }

    const items = this._getItems(this.itemsSlot as HTMLSlotElement);
    for (let i = 0; i < items.length; i += 1) {
      if (_changedProperties.has('variant')) {
        items[i].groupVariant = this.variant;
      }

      if (_changedProperties.has('size')) {
        items[i].size = this.size;
      }

      if (_changedProperties.has('horizontalPadding')) {
        items[i].horizontalPadding = this.horizontalPadding;
      }
    }
  }

  protected renderGroupTitle() {
    if (!this.groupTitle) {
      return undefined;
    }

    return html` <h4 class="group-title">${this.groupTitle}</h4> `;
  }

  protected render() {
    const role = this.innerRole || 'listbox';
    const ariaLabel = this.innerAriaLabel || 'list';
    const isNested = this.groupTitle && this.id !== 'hds-list-1';
    const classes = {
      truncate: this.truncate,
      list: true,
      nested: isNested,
    };
    classes[`hds-list-variant-${this.variant}`] = true;
    const localStyles = {
      maxWidth: 'initial',
      width: this.width,
    };

    return html`
      <div
        class="-hds-list-wrapper ${classMap(classes)}"
        style="${styleMap(localStyles)}"
        tabindex="0"
        role="${role}"
        aria-label="${ariaLabel}"
        @select="${ifDefined(!isNested ? this.onSelect : undefined)}"
        @keydown="${ifDefined(!isNested ? this.onKeypress : undefined)}"
      >
        ${this.renderGroupTitle()}
        <slot @slotchange="${this._onSlotChange}"></slot>
      </div>
    `;
  }

  protected async onKeypress(evt: KeyboardEvent) {
    evt.preventDefault();
    const listItems = this._getAllItems();
    const itemsLen = listItems.length;
    const currentSelectedItem = this._getFocusedItem();
    const index = currentSelectedItem ? this._getItemIndex(currentSelectedItem.id) : -1;

    // eslint-disable-next-line default-case
    switch (evt.key) {
      case 'ArrowUp':
        {
          // Up arrow
          const prevIndex = index - 1;

          if (prevIndex > -1) {
            const item = listItems[prevIndex];
            (item.shadowRoot?.querySelector('div[role="option"]') as HTMLElement).focus();
          }
        }
        break;
      case 'ArrowDown':
        {
          // Down arrow
          const nextIndex = index + 1;

          if (nextIndex < itemsLen) {
            const item = listItems[nextIndex];
            (item.shadowRoot?.querySelector('div[role="option"]') as HTMLElement).focus();
          }
        }
        break;
      case 'Enter':
        if (currentSelectedItem && index > -1) {
          this.selectedIndex = index;
        }
        break;
    }
  }

  protected async onSelect(e: CustomEvent<ListItemSelectPayload>) {
    const selectedItemId = e.detail;
    e.stopPropagation();
    const selectedElement = this._getAllItems()[this.selectedIndex];
    if (!selectedElement || selectedItemId !== selectedElement.id) {
      this.selectedIndex = this._getItemIndex(selectedItemId);
    }
  }

  private async _onSlotChange() {
    await this.updateComplete;
    if (this.selectedIndex === -1) {
      this.selectedIndex = this._getAllItems().findIndex(item => item.selected);
    }
  }

  private _getFocusedItem() {
    return this._getAllItems().filter(item => item === document.activeElement)[0];
  }

  private _getItemIndex(itemId: string) {
    return this._getAllItems().findIndex(item => item.id === itemId);
  }

  // Get list items from a list group
  private _getItems(itemsSlot: HTMLSlotElement) {
    if (!itemsSlot) return [];
    return itemsSlot
      .assignedNodes({flatten: true})
      .filter((e: Node) => e instanceof ListItem) as ListItem[];
  }

  // Get list items from array of list groups
  private _getGroupedItems(itemsSlot: HTMLSlotElement) {
    if (!itemsSlot) return [];
    const listGroups = (itemsSlot as HTMLSlotElement)
      .assignedNodes({flatten: true})
      .filter((e: Node) => e instanceof List) as List[];

    return listGroups.reduce((prevList, currentList) => {
      const groupItems = this._getItems(currentList.itemsSlot as HTMLSlotElement);
      return [...prevList, ...groupItems];
    }, [] as ListItem[]);
  }

  // get all the list items from all the list groups
  private _getAllItems() {
    return [
      ...this._getItems(this.itemsSlot as HTMLSlotElement),
      ...this._getGroupedItems(this.itemsSlot as HTMLSlotElement),
    ];
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-list': List;
  }
}
